/**
 * WordPress Dependencies
 */
 const { __ } = wp.i18n;
 const { addFilter } = wp.hooks;
 const { Fragment }	= wp.element;
 const { InspectorAdvancedControls, InnerBlocks }	= wp.blockEditor;
 const { createHigherOrderComponent } = wp.compose;
 const { TextControl, ToggleControl, BaseControl, SelectControl } = wp.components;

 export const CAPTION_STYLES = [
	{ value: 'none', label: __( 'No Automatic Captions', 'gutenslider' ) },
	{ value: 'caption-only', label: __( 'Caption Only', 'gutenslider' ) },
	{ value: 'title-caption', label: __( 'Title - Caption', 'gutenslider' ) },
	{ value: 'title-caption-description', label: __( 'Title - Caption - Description', 'gutenslider' ) },
];

 /**
	* Add mobile visibility controls on Advanced Block Panel.
	*
	* @param {function} BlockEdit Block edit component.
	*
	* @return {function} BlockEdit Modified block edit component.
	*/
 const gutensliderWithAdvancedControls = createHigherOrderComponent( ( BlockEdit ) => {
	 return ( props ) => {
		 const {
			 attributes,
			 setAttributes,
			 isSelected,
		 } = props;

		 const {
			 visibleOnDesktop,
			 visibleOnTablet,
			 visibleOnMobile,
			 gsBlockId,
			 hashId,
			 hashNavigation,
		 } = attributes;

		 if ( props.name === 'eedee/block-gutenslider' ) {
			 const updateCaptions = ( val ) => {
				 setAttributes( { autoCaption: val } );
			 };

			 const { autoCaption } = attributes;

			 return (
				 <Fragment>
					 <BlockEdit { ...props } />
					 { isSelected &&
						 <InspectorAdvancedControls>
							 <BaseControl
								 label={ __( 'Slider Id', 'gutenslider' ) }
							 >
								 <code style={ { display: 'block' } }>{ `gutenslider-${ gsBlockId }` }</code>
							 </BaseControl>
							 <SelectControl
								 label={ __( 'Automatic Captions', 'gutenslider' ) }
								 value={ autoCaption }
								 options={ CAPTION_STYLES }
								 onChange={ ( val ) => updateCaptions( val ) }
								 help={ __( 'Changing this will eventually add a paragraph block to all slides, that contains the selected content', 'gutenslider' ) }
							 />
							 <ToggleControl
								 label={ __( 'Show On Desktop' ) }
								 checked={ !! visibleOnDesktop }
								 onChange={ () => setAttributes( { visibleOnDesktop: ! visibleOnDesktop } ) }
								 help={ !! visibleOnDesktop ? __( 'Slider is shown on large screens.', 'gutenslider' ) : __( 'Slider is hidden on large screens.', 'gutenslider' ) }
							 />
							 <ToggleControl
								 label={ __( 'Show On Tablet' ) }
								 checked={ !! visibleOnTablet }
								 onChange={ () => setAttributes( { visibleOnTablet: ! visibleOnTablet } ) }
								 help={ !! visibleOnTablet ? __( 'Slider is shown on medium screens.', 'gutenslider' ) : __( 'Slider is hidden on medium screens.', 'gutenslider' ) }
							 />
							 <ToggleControl
								 label={ __( 'Show On Mobile' ) }
								 checked={ !! visibleOnMobile }
								 onChange={ () => setAttributes( { visibleOnMobile: ! visibleOnMobile } ) }
								 help={ !! visibleOnMobile ? __( 'Slider is shown on small screens.', 'gutenslider' ) : __( 'Slider is hidden on small screens.', 'gutenslider' ) }
							 />
							 <ToggleControl
								label={ __( 'Enable Hash Navigation', 'gutenslider' ) }
								checked={ hashNavigation }
								onChange={ ( val ) => setAttributes( { hashNavigation: val } ) }
							/>
							{ hashNavigation && <TextControl
								 label={ __( 'Slider Hash Id', 'gutenslider' ) }
								 help={ __( 'The id that shows in the browser location bar suffixed by the slide hash id you can select in the slide settings', 'gutenslider' ) }
								 value={ hashId }
								 onChange={ ( val ) => setAttributes( { hashId: val } ) }
							 /> }
						 </InspectorAdvancedControls>
					 }
				 </Fragment>
			 );
		 }

		 if ( props.name === 'eedee/block-gutenslide' ) {
			 return (
				 <Fragment>
					 <BlockEdit { ...props } />
					 { isSelected &&
						 <InspectorAdvancedControls>
							 <BaseControl
								 label={ __( 'Slide Id', 'gutenslider' ) }
							 >
								 <code style={ { display: 'block' } }>{ `gutenslide-${ gsBlockId }` }</code>
							 </BaseControl>
							 <TextControl
								 label={ __( 'Slide Hash Id', 'gutenslider' ) }
								 help={ __( 'The slide id that shows in the browser location bar', 'gutenslider' ) }
								 value={ hashId }
								 onChange={ ( val ) => setAttributes( { hashId: val } ) }
							 />
						 </InspectorAdvancedControls>
					 }
				 </Fragment>
			 );
		 }

		 return <BlockEdit { ...props } />;
	 };
 }, 'gutensliderWithAdvancedControls' );

 addFilter(
	 'editor.BlockEdit',
	 'eedee/gutenslider',
	 gutensliderWithAdvancedControls
 );

